/*
KISSY CSS Reset
理念：1. reset 的目的不是清除浏览器的默认样式，这仅是部分工作。清除和重置是紧密不可分的。
2. reset 的目的不是让默认样式在所有浏览器下一致，而是减少默认样式有可能带来的问题。
3. reset 期望提供一套普适通用的基础样式。但没有银弹，推荐根据具体需求，裁剪和修改后再使用。
特色：1. 适应中文；2. 基于最新主流浏览器。

 */


/** 清除内外边距 **/

body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,

/* structural elements 结构元素 */

dl,
dt,
dd,
ul,
ol,
li,

/* list elements 列表元素 */

pre,

/* text formatting elements 文本格式元素 */

form,
fieldset,
legend,
button,
input,
textarea,

/* form elements 表单元素 */

th,
td
/* table elements 表格元素 */

{
	margin: 0;
	padding: 0;
}


/** 设置默认字体 **/

body,
button,
input,
select,
textarea
/* for ie */

{
	font-family: "Microsoft YaHei", Tahoma, Verdana, SimSun;
	/*font-size:12px;*/
	background-color: white;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: 100%;
}

address,
cite,
dfn,
em,
var {
	font-style: normal;
}


/* 将斜体扶正 */

code,
kbd,
pre,
samp {
	font-family: courier new, courier, monospace;
}


/* 统一等宽字体 */

small {
	font-size: 12px;
}


/* 小于 12px 的中文很难阅读，让 small 正常化 */


/** 重置列表元素 **/

ul,
ol {
	list-style: none;
}


/** 重置文本格式元素 **/

a {
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}


/** 重置表单元素 **/

legend {
	color: #000;
}


/* for ie6 */

fieldset,
img {
	border: 0;
}


/* img 搭车：让链接里的 img 无边框 */

button,
input,
select,
textarea {
	font-size: 100%;
	outline: none;
}


/* 使得表单元素在 ie 下能继承字体大小 */


/* 注：optgroup 无法扶正 */


/** 重置表格元素 **/

table {
	border-collapse: collapse;
	border-spacing: 0;
}


/* 清除浮动 */

.ks-clear:after,
.clear:after {
	content: '\20';
	display: block;
	height: 0;
	clear: both;
}

.ks-clear,
.clear {
	*zoom: 1;
}

.cb {
	clear: both;
}


/*左右浮动*/

.pull_left {
	float: left;
}

.pull_right {
	float: right;
}


/*手型*/

.point {
	cursor: pointer;
}


/*input样式*/

.bxdIpt,
.bxdSelect {
	color: #333;
	font-family: "Microsoft YaHei", Tahoma, Verdana, SimSun;
	padding: 4px;
	font-size: 15px;
	outline-style: none;
	outline-color: invert;
	border-radius: 3px;
	text-shadow: 0px 1px 2px #fff;
	background-size: auto;
	background-origin: padding-box;
	background-clip: border-box;
	background-color: rgb(255, 255, 255);
	margin-right: 8px;
	border: 1px solid #ccc;
}

.bxdIpt:focus,
.bxdSelect:focus {
	border: 1px solid #fafafa;
	-webkit-box-shadow: 0px 0px 6px #007eff;
	-moz-box-shadow: 0px 0px 5px #007eff;
	box-shadow: 0px 0px 5px #007eff;
	-webkit-transition: border linear .2s, box-shadow linear .2s;
	-moz-transition: border linear .2s, box-shadow linear .2s;
	-o-transition: border linear .2s, box-shadow linear .2s;
	transition: border linear .2s, box-shadow linear .2s;
}

.bxdSelect {
	padding-left: 20px;
	padding-right: 12px;
}


/*蒙层*/

.mc {
	position: fixed;
	width: 100%;
	height: 100%;
	background: #10100F;
	opacity: 0.8;
}


/*字间距*/

body {
	letter-spacing: 2px;
}


/*长按钮字体之间有间距*/


/*<div class="bigBtn">
	<span>登</span>
	<span class="bigBtnMidSpan"></span>
	<span>录</span>
</div>*/

.bigBtn {
	text-align: center;
	width: 20%;
	height: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0 auto;
	color: #FFFFFF;
	background: #3385ff;
	font-size: 18px;
	border-radius: 8px;
}

.bigBtn:hover {
	opacity: 0.88;
	cursor: pointer;
	border: 1px solid #fafafa;
	-webkit-box-shadow: 0px 0px 2px #FFFFFF;
	-moz-box-shadow: 0px 0px 2px #FFFFFF;
	box-shadow: 0px 0px 2px #FFFFFF;
	-webkit-transition: border linear .2s, box-shadow linear .2s;
	-moz-transition: border linear .2s, box-shadow linear .2s;
	-o-transition: border linear .2s, box-shadow linear .2s;
	transition: border linear .2s, box-shadow linear .2s;
}


/*按钮两个字之间的间距*/

.bigBtnMidSpan {
	padding-left: 20px;
}


/*超出换行*/

.overWrap {
	width: 200px;
	/*这里假设一个宽度*/
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}


/*一些命名规范问题*/


/*写CSS时,0后不带单位,小数省略0,先写结构位置层position,float,margin等,再写表现层,color,background等*/

a,
button,
input {
	-webkit-tap-highlight-color: rgba(255, 0, 0, 0);
}



